import { combineReducers } from 'redux';
import { ADD_BASKET, REMOVE_BASKET, ADD_ROOM, REMOVE_ROOM, ADD_DATA, REMOVE_DATA, ADD_FILM } from '../constants/config';

const basket = {
  data: []
}
function dataBasket(state = basket, action){
switch (action.type) {

  case ADD_BASKET:
    return {
      ...state,
      data: [...state.data, action.data]
    }
    case REMOVE_BASKET:
      return {
        ...state,
        data: action.test
      }

    default:
      return state;
  }
}

const activRooms = {
  data: []
}
function Rooms(state = activRooms, action){

switch (action.type) {

  case ADD_ROOM:
    return {
      ...state,
      data: [...state.data, action.data]
    }
    case REMOVE_ROOM:
      return {
        ...state,
        data: action.test
      }

    default:
      return state;
  }
}
const initialHall = {
  data: []
}
function Hall(state = initialHall, action){
console.log('data',action.data);
switch (action.type) {
  case ADD_DATA:
    return {
      ...state,
      data: action.data
    }
    case REMOVE_DATA:
      return {
        ...state,
        data: action.data
      }

    default:
      return state;
  }
}
const initialData = {
  data: [
    {
      image: 'https://planetakino.ua/f/1/movies/avengers_infinity_war_original/Avengers-poster-small.jpg',
      time: '19.03.2018',
      text: 'Avengers: Infinity War ( ) (12+)',
      counter: '40'
    },
    {
      image: 'https://planetakino.ua/f/1/movies/winchester/Winchester-poster2-small.jpg',
      time: '23.03.2018',
      text: '. ,   (16+)',
      counter: '40'
    },
    {
      image: 'https://planetakino.ua/f/1/movies/ready_player_one/ready_player_one_post3-small.jpg',
      time: '21.03.2018',
      text: '   (12+)',
      counter: '40'
    }
  ]
};
function filmData(state = initialData, action){

  switch (action.type) {
    case ADD_FILM:
      return {
        ...state
      }
    default:
      return state;
  }
}


const usersInitialState = {
  loading: false,
  loaded: false,
  data: [],
  dataUsers: [],
  errors: []
};



const reducer = combineReducers({
  filmData,
  Hall,
  Rooms,
  dataBasket
});

export default reducer;



